import { NextRequest } from "next/server";
import { JWTPayload, SignJWT, jwtVerify } from "jose";
import { User } from "../models/user";

export class AuthService {
  public static async signIn(
    email: string,
    password: string
  ): Promise<User | null> {
    const user = await Prisma.user.findFirst({
      where: {
        email: email,
        password: password,
      },
    });
    return user;
  }
  public static async checkAuth(req: NextRequest): Promise<false | JWTPayload> {
    const token = req.cookies.get("auth-token")?.value;
    if (!token) {
      return false;
    }
    try {
      const { payload } = await jwtVerify(
        token,
        new TextEncoder().encode(process.env.AUTH_JWT_SECRET)
      );

      return payload;
    } catch (error) {
      return false;
    }
  }

  public static async generateToken(payload: JWTPayload): Promise<string> {
    return await new SignJWT(payload).sign(
      new TextEncoder().encode(process.env.AUTH_JWT_SECRET)
    );
  }
}
